@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.card-question {
  &__inner {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    padding: 0 20px 16px;
    border-bottom: 1px solid $color_border_secondary;

    @include media(from_sm) {
      padding: 0 20px 40px;
    }

    @include media(from_m) {
      padding: 0 20px 16px 10px;
    }

    @include media(from_l) {
      padding: 0 40px 24px 16px;
    }
  }

  &__toggler {
    position: absolute;
    top: 0;
    right: 20px;

    @include media(from_l) {
      right: 40px;
    }
  }

  &__title {
    width: 100%;
    box-sizing: border-box;
    padding-right: 32px;
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: $blue-light;

    transition: 0.2s ease-in-out;

    @include media(from_sm) {
      font-size: 30px;
      padding-right: 48px;
    }

    @include media(from_m) {
      font-size: 16px;
    }

    @include media(from_l) {
      font-size: 20px;
    }
  }

  &__answer {
    display: none;
    width: 100%;
    box-sizing: border-box;
    
    @include media(from_m) {
      padding-right: 96px;
    }

    @include media(from_l) {
      padding-right: 104px;
    }
  }

  &__answer-row {
    display: flex;
  }

  &__pic-wrapper {
    display: none;

    @include media(from_m) {
      display: block;
      flex-shrink: 0;
      width: 56px;
      height: 56px;
      margin-top: 4px;
      margin-right: 16px;
      border-radius: 50%;
      overflow: hidden;
    }

    @include media(from_l) {
      width: 72px;
      height: 72px;
      margin-top: 8px;
      margin-right: 24px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__text {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color_text_secondary;

    @include media(from_sm) {
      font-size: 24px;
    }

    @include media(from_m) {
      font-size: 12px;
    }

    @include media(from_l) {
      font-size: 16px;
    }
  }
}

.card-question_expanded {
  .card-question {
    &__inner {
      padding-top: 15px;
      padding-bottom: 50px;
      border-radius: 20px;
      overflow: hidden;
      background-color: #fff;

      border-color: transparent;
      transition: background-color 0.2s ease-in-out;

      @include media(from_sm) {
        padding-top: 32px;
        padding-bottom: 64px;
      }

      @include media(from_m) {
        width: calc(100% + 14px);
        padding-top: 20px;
        padding-bottom: 40px;
        padding-left: 24px;
        transform: translateX(-14px);
      }

      @include media(from_l) {
        width: calc(100% + 16px);
        padding-top: 24px;
        padding-left: 32px;
        transform: translateX(-16px);
      }
    }

    &__toggler {
      transform: translateY(15px) rotate(45deg);
      background-size: 20px auto;

      @include media(from_sm) {
        transform: translateY(32px) rotate(45deg);
        background-size: 28px auto;
      }
  
      @include media(from_m) {
        transform: translateY(18px) rotate(45deg);
        background-size: 20px auto;
      }
  
      @include media(from_l) {
        transform: translateY(20px) rotate(45deg);
        background-size: 28px auto;
      }
    }

    &__title { 
      margin: 0 0 24px;
      color: $black;

      @include media(from_sm) {
        margin: 0 0 48px;
      }
  
      @include media(from_m) {
        margin: 0 0 24px;
      }
  
      @include media(from_l) {
        margin: 0 0 20px;
      }
    }

    &__answer {
      display: block;
      animation: fadeIn 0.2s ease-in-out;
    }
  }
}

.card-question_collapsed {
  .card-question {
    &__answer {
      display: none;
      animation: fadeOut 0.2s ease-in-out;
    }
  }
}
