@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-purpose{
    margin-bottom: 140px;
    
    @include media(from_sm) {
        margin-bottom: 212px;
    }
    
    @include media(from_m) {
        margin-bottom: 235px;
    }
    
    @include media(from_l) {
        margin-bottom: 318px;
    }
    
    &__inner{
        margin: 0 auto;
        width: 100%;

        @include media(from_sm) {
            max-width: 514px;
        }

        @include media(from_m) {
            max-width: 605px;
        }

        @include media(from_l) {
            max-width: 795px;
        }
    }

    &__title{
        padding: 0;
        margin: 0;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 5px;
        color: #C1C8CC;
        margin-bottom: 30px; 
        
        @include media(from_sm) {
            font-size: 25px;
            letter-spacing: 8.0828px; 
            margin-bottom: 48px; 
        }
        
        @include media(from_m) {
            font-size: 12px;
            letter-spacing: 3.8px;
            margin-bottom: 28px;
        }
        
        @include media(from_l) {
            font-size: 16px;
            letter-spacing: 5px;
            margin-bottom: 26px;
        }
    }

    &__text{
        padding: 0;
        margin: 0;
        text-align: center;
        font-weight: 300;
        font-size: 20px;
        line-height: 150%;
        letter-spacing: 0.15px;
        color: #000;
        
        @include media(from_sm) {
            font-size: 32px;
            letter-spacing: 0.2px;
        }
        
        @include media(from_m) {
            font-size: 22px;
            letter-spacing: 0.1px;
        }
        
        @include media(from_l) {
            font-size: 30px;
            letter-spacing: 0.15px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        

    }
}