@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.badges {
    display: inline-flex;
    align-items: center;
    padding: 4px 3px;
    border-radius: 50px;
    background-color: #fff;
    overflow: auto hidden;
    width: auto;
    max-width: 100%;

    &__item {
        margin-right: 8px;

        &:last-of-type{
            margin-right: 0;
        }
    }
}
.badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 42px;
    padding: 0 24px;
    border-radius: 40px;
    text-decoration: none;
    color: rgba(0, 77, 150, 0.3);
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.1px;
    background-color: transparent;
    transition: color 0.25s ease 0.1s, fill 0.25s ease 0.1s, background-color 0.35s ease;

    
    @include media(from_sm) {
        font-size: 22px;
    }
    
    @include media(from_m) {
        font-size: 10px;
    }
    
    @include media(from_l) {
        font-size: 14px;
    }

    &__text {
    }

    &.is-active,
    &:hover {
        background-color: $blue;
        color: #fff;
    }
}