@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.section-features {
  &__outer-indent {
    margin-bottom: 120px;

    @include media(from_sm) {
      margin-bottom: 200px;
    }

    @include media(from_m) {
      margin-bottom: 160px;
    }

    @include media(from_l) {
      margin-bottom: 240px;
    }
  }

  &__inner {
    width: 100%;
    box-sizing: border-box;
    padding: 22px 24px;
    background-color: #fff;
    border-radius: 20px;
    overflow: hidden;

    @include media(from_sm) {
      padding: 32px 40px 48px;
      border-radius: 32px;
    }

    @include media(from_m) {
      padding: 40px 48px 64px;
      border-radius: 24px;
    }

    @include media(from_l) {
      padding: 48px 64px;
      border-radius: 40px;
    }
  }

  &__row {
    display: flex;
    flex-direction: column;
    margin-top: -35px;

    @include media(from_sm) {
      margin-top: -52px;
    }

    @include media(from_m) {
      flex-direction: row;
      margin-top: 0;
      margin-left: -112px;
    }

    @include media(from_l) {
      margin-left: -142px;
    }
  }

  &__col {
    margin-top: 35px;

    @include media(from_sm) {
      margin-top: 52px;
    }

    @include media(from_m) {
      display: flex;
      flex-direction: column;

      margin-top: 0;
      margin-left: 112px;
    }

    @include media(from_l) {
      margin-left: 142px;
    }
  }
  
  &__col_type_left {
    @include media(from_m) {
      flex-basis: 306px;
    }

    @include media(from_l) {
      flex-basis: 403px;
    }
  }

  &__col_type_right {
    @include media(from_m) {
      flex-basis: 350px
    }

    @include media(from_l) {
      flex-basis: 462px;
    }
  }

  & .section__title {
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 120%;

    @media screen and (min-width: 375px){
      font-size: 24px;
    }

    @include media(from_sm) {
      margin-bottom: 32px;
      font-size: 40px;
    }

    @include media(from_m) {
      margin-bottom: 20px;
      font-size: 22px;
    }

    @include media(from_l) {
      margin-bottom: 32px;
      font-size: 30px;
    }
  }

  &__link {
    padding-left: 6px;
    text-decoration: none;
    color: $blue-light;

    @include defaultHover();

    @include media(from_sm) {
      padding-left: 8px;
    }

    @include media(from_m) {
      display: inline-block;
      padding-left: 0;
    }
  }

  &__desc {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color_text_secondary;

    @include media(from_sm) {
      font-size: 22px;
    }

    @include media(from_m) {
      margin: 0 0 64px;
      font-size: 12px;
    }

    @include media(from_l) {
      margin: 0 0 88px;
      font-size: 16px;
    }
  }

  &__partner {
    display: none;

    @include media(from_m) {
      display: block;
      margin-top: auto;
    }
  }

  & .socials__partner-logo {
    width: 180px;

    @include media(from_l) {
      width: 240px;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    margin-top: -32px;

    @include media(from_sm) {
      margin-top: -56px;
    }

    @include media(from_m) {
      margin-top: -40px;
    }

    @include media(from_l) {
      margin-top: -32px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 32px;

    @include media(from_sm) {
      margin-top: 56px;
    }

    @include media(from_m) {
      margin-top: 40px;
    }

    @include media(from_l) {
      margin-top: 32px;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    width: 56px;
    height: 56px;
    margin-right: 24px;

    background-repeat: no-repeat;
    background-position: left 0 top 50%;

    @include media(from_sm) {
      width: 90px;
      height: 90px;
    }

    @include media(from_m) {
      width: 72px;
      height: 72px;
    }

    @include media(from_l) {
      width: 110px;
      height: 110px;
      margin-right: 16px;
    }
  }

  &__item-icon_type_1 {
    background-size: 53px auto;

    background-image: url("data:image/svg+xml,%3Csvg width='53' height='55' viewBox='0 0 53 55' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M46 55C46 43.9543 37.2696 35 26.5 35C15.7304 35 7 43.9543 7 55' stroke='%2300EBBA' stroke-width='13.4146'/%3E%3Cpath d='M26.5 22C32.299 22 37 17.0751 37 11C37 4.92487 32.299 0 26.5 0C20.701 0 16 4.92487 16 11C16 17.0751 20.701 22 26.5 22Z' fill='%2300EBBA'/%3E%3C/svg%3E%0A");

    @include media(from_sm) {
      background-size: 86px auto;
    }

    @include media(from_m) {
      background-size: 60px auto;
    }

    @include media(from_l) {
      background-size: 78px auto;
    }
  }
  
  &__item-icon_type_2 {
    background-size: 55px auto;

    background-image: url("data:image/svg+xml,%3Csvg width='55' height='56' viewBox='0 0 55 56' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M27.5 49.4322C39.2643 49.4322 48.4322 39.6294 48.4322 28C48.4322 16.3706 39.2643 6.56784 27.5 6.56784C15.7357 6.56784 6.56784 16.3706 6.56784 28C6.56784 39.6294 15.7357 49.4322 27.5 49.4322Z' stroke='%23FFD600' stroke-width='12.8643'/%3E%3Cpath d='M32.9002 35.2L27.8765 32.14L22.8527 35.2L24.1829 29.428L19.753 25.552L25.5962 25.048L27.8765 19.6L30.1568 25.036L36 25.54L31.5701 29.416L32.9002 35.2Z' fill='%23FFD600'/%3E%3C/svg%3E%0A");

    @include media(from_sm) {
      background-size: 87px auto;
    }

    @include media(from_m) {
      background-size: 66px auto;
    }

    @include media(from_l) {
      background-size: 85px auto;
    }
  }

  &__item-icon_type_3 {
    background-size: 55px auto;

    background-image: url("data:image/svg+xml,%3Csvg width='55' height='57' viewBox='0 0 55 57' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23 40.4322C32.3674 40.4322 39.4322 32.552 39.4322 23.5C39.4322 14.448 32.3674 6.56784 23 6.56784C13.6326 6.56784 6.56784 14.448 6.56784 23.5C6.56784 32.552 13.6326 40.4322 23 40.4322Z' stroke='%233AB3FE' stroke-width='12.8643'/%3E%3Cpath d='M32 34L49 51' stroke='%233AB3FE' stroke-width='16.0804'/%3E%3C/svg%3E%0A");

    @include media(from_sm) {
      background-size: 87px auto;
    }

    @include media(from_m) {
      background-size: 67px auto;
    }

    @include media(from_l) {
      background-size: 88px auto;
    }
  }

  &__item-desc {
    flex-grow: 1;
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    color: $black;

    @include media(from_sm) {
      font-size: 22px;
    }

    @include media(from_m) {
      font-size: 16px;
    }

    @include media(from_l) {
      font-size: 20px;
    }
  }

  &__item-label {
    position: absolute;
    bottom: -16px;
    left: 80px;
    transform: rotate(-2deg);
    padding: 4px 12px;
    background: #00EBBA;
    border-radius: 70px;

    font-weight: 700;
    font-size: 10px;
    line-height: 150%;

    color: $white;

    @media screen and (min-width: 375px){
      bottom: -4px;
      left: 80px;
      padding: 4px 12px;
      border-radius: 70px;
  
      font-size: 14px;
      color: $white;
    }

    @include media(from_sm) {
      bottom: 0;
      left: 114px;
      font-size: 22px;
    }

    @include media(from_m) {
      bottom: unset;
      top: 40px;
      left: 96px;
      font-size: 12px;
      font-weight: 800;
    }

    @include media(from_l) {
      top: 48px;
      left: 134px;
      font-size: 16px;
    }
  }
}
