@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.footer {
  background-color: #fff;

  &__inner {
    padding: 32px 0 24px;

    @include media(from_sm) {     
      padding: 56px 0 48px;
    }
  }

  &__container {
    padding: 0 32px;

    @include media(from_sm) {
      padding: 0 85px;
    }

    @include media(from_m) {
      position: relative;
    }

    @include media(from_l) {
      max-width: 1370px;
    }
  }

  &__sitemap {
    display: none;

    @include media(from_m) {
      display: block;
      padding-bottom: 32px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }

    @include media(from_l) {
      padding-bottom: 56px;
    }
  }

  &__menu {
    display: flex;
    flex-direction: column;
  }

  &__menu_type_main {
    display: block;
    box-sizing: border-box;
    padding: 16px 0 0 48px;
  }

  &__menu-title {
    margin: 0 0 8px;
    font-weight: 700;
    font-size: 12px;
    line-height: 120%;

    @include media(from_l) {
      font-size: 14px;
    }
  }

  &__nav {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__nav_type_cols {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 0 -24px;
  }

  &__nav-item {
    flex-basis: 100%;
  }

  &__nav_type_cols &__nav-item {
    flex-basis: calc(50% - 24px);
    margin-left: 24px;
  } 

  &__nav-link {
    text-decoration: none;
    color: $blue;
    font-size: 10px;
    line-height: 200%;

    @include defaultHover();

    @include media(from_l) {
      font-size: 14px;
    }
  }

  &__company-info {
    @include media(from_m) {
      padding-top: 24px;
    }

    @include media(from_l) {
      padding-top: 32px;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    
    @include media(from_m) {
      margin-left: -32px;
      margin-top: -16px;
    }
  }

  &__company-info &__row {
    @include media(from_m) {
      width: calc(100% - 212px);
      align-items: center;
    }
  }

  &__col {
    flex-basis: 100%;

    @include media(from_m) {
      flex-basis: 212px;
      flex-shrink: 0;
      margin-left: 32px;
      margin-top: 16px;
    }

    @include media(from_l) {
      flex-basis: 288px;
    }
  }

  &__col_order_second-last {
    order: 1;

    @include media(from_m) {
      order: unset;
    }
  }

  &__col_order_last {
    order: 2;

    @include media(from_m) {
      order: unset;
    }
  }

  &__col_size_m {
    @include media(from_m) {
      flex-basis: 240px;
    }

    @include media(from_l) {
      flex-basis: 288px;
    }
  }

  &__col_size_l {
    @include media(from_m) {
      flex-basis: 400px;
    }

    @include media(from_l) {
      flex-basis: 528px;
    }
  }

  &__col_pos_bottom {
    @include media(from_m) {
      position: absolute;
      top: 24px;
      right: 85px;
    }
  }

  & .contacts {
    padding-bottom: 24px;

    @include media(from_sm) {     
      padding-bottom: 40px;
    }

    @include media(from_m) {
      padding-bottom: 0;
    }
  }

  & .contacts__label {
    font-size: 14px;

    @include media(from_sm) {
      font-size: 20px;
    }

    @include media(from_m) {
      font-size: 12px;
    }

    @include media(from_l) {
      font-size: 14px;
    }
  }

  & .contacts__row {
    @include media(from_m) {
      flex-direction: column;
      align-items: unset;
    }
  }

  & .contacts__col {
    margin-right: 24px;

    @include media(from_sm) {
      margin-right: 48px;
    }

    @include media(from_m) {
      margin-right: 0;
    }
  }

  & .contacts__link {
    @include media(from_sm) {
      font-size: 20px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      font-size: 14px;
    }
  }

  & .contacts__text {
    @include media(from_sm) {
      font-size: 20px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      font-size: 12px;
    }
  }

  &__tel {
    @include media(from_m) {
      color: $color_text_secondary;
    }
  }

  &__email {
    display: flex;
  }

  &__email .contacts__label {
    margin: 0 16px 0 0;

    @include media(from_sm) {
      margin: 0 24px 0 0;
    }

    @include media(from_m) {
      margin: 0 8px 0 0;
    }
  } 

  & .socials {
    display: flex;
    align-items: flex-end;

    padding-top: 24px;
    padding-bottom: 32px;

    @include media(from_sm) {     
      padding-top: 32px;
      padding-bottom: 64px;
    }

    @include media(from_m) {
      flex-direction: column;
      align-items: unset;
      padding: 0;
    }
  }

  & .socials__media {
    @include media(from_l) {
      margin-left: -5px;
    }
  }

  & .socials__media-item {
    @include media(from_l) {
      margin-left: 5px;
    }
  }

  & .socials__partner-label {
    @include media(from_sm) {     
      font-size: 18px;
    }

    @include media(from_m) {
      font-size: 12px;
    }
  }

  & .socials__partner-logo {
    width: 190px;
    margin-right: 16px;

    @include media(from_sm) {     
      width: 300px;
      margin-right: 32px;
    }

    @include media(from_m) {
      width: 112px;
      margin-right: 0;
      margin-bottom: 8px;
    }
  }

  &__politic {
    width: 100%;
    max-width: 272px;
    padding-bottom: 8px;

    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 120%;
    color: $color_text_secondary;

    @include media(from_sm) {
      max-width: 392px;
      padding-bottom: 24px;
      
      font-size: 20px;
    }

    @include media(from_m) {
      max-width: 100%;
      padding: 32px 0 0 0;
      font-size: 10px;
    }

    @include media(from_l) {
      max-width: 232px;
      font-size: 12px;
    }
  }

  &__politic-link {
    text-decoration: none;
    color: $blue-light;

    @include defaultHover();
  }

  &__rights {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 120%;
    color: $color_text_secondary;

    @include media(from_sm) {     
      font-size: 20px;
    }

    @include media(from_m) {
      display: flex;
      align-items: center;
      max-width: 176px;
      font-size: 10px;
    }

    @include media(from_l) {
      min-width: 204px;
      font-size: 12px;
    }
  }

  &__rights-year {
    padding-right: 8px;

    @include media(from_m) {
      flex-shrink: 0;
    }
  }
}
