@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.resolution {
  width: 100%;
  box-sizing: border-box;
  padding: 16px;
  border: 5px solid $blue-light;

  @include media(from_sm) {
    padding: 24px 16px;
    border-width: 8px;
  }

  @include media(from_m) {
    position: relative;
    padding: 24px 34px 24px 190px;
  }

  @include media(from_l) {
    padding: 32px 54px 32px 272px;
    border-width: 10px;
  }

  &__title-wrapper {
    display: flex;
    align-items: center;
    margin: 0 0 24px;

    @include media(from_l) {
      margin: 0 0 32px;
    }
  }

  &__icon {
    flex-shrink: 0;
    width: 70px;
    height: 70px;
    margin-right: 10px;

    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    background-image: url(../../assets/media/emblem.png);

    @include media(from_sm) {
      width: 112px;
      height: 112px;
      margin-right: 32px;
    }

    @include media(from_m) {
      position: absolute;
      top: 50%;
      left: 32px;
      transform: translateY(-50%);
      width: 120px;
      height: 120px;
      margin: 0;
    }

    @include media(from_l) {
      width: 156px;
      height: 156px;
      left: 64px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;

    color: $black;

    @include media(from_sm) {
      font-size: 24px;
    }

    @include media(from_m) {
      font-size: 16px;
    }

    @include media(from_l) {
      font-size: 20px;
    }
  }

  &__desc {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: rgba(#000000, 0.6);

    @include media(from_sm) {
      font-size: 24px;
    }

    @include media(from_m) {
      font-size: 12px;
    }

    @include media(from_l) {
      font-size: 16px;
    }
  }
}
