@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.card-specialist {
  &__inner {
    position: relative;
    background-color: #fff;
    overflow: hidden;
    border-radius: 20px;

    @include media(from_sm) {
      border-radius: 16px;
    }

    @include media(from_m) {
      display: flex;
      padding-left: 305px;
      min-height: 332px;
      border-radius: 16px;
    }

    @include media(from_l) {
      padding-left: 400px;
      min-height: 437px;
      border-radius: 20px;
    }
  }

  &__pic-wrapper {
    height: 148px;
    border-radius: 20px 20px 0px 0px;
    overflow: hidden;
    background-color: #C4C4C4;

    @include media(from_sm) {
      height: 240px;
      border-radius: 32px 32px 0px 0px;
    }

    @include media(from_m) {
      position: absolute;
      top: 0;
      left: 0;
      width: 305px;
      height: 100%;
      border-radius: 16px 0px 0px 16px;
    }

    @include media(from_l) {
      width: 400px;
      height: 100%;
      border-radius: 20px 0px 0px 20px;
    }
  }

  &__pic {
    width: 100%;
    height: auto;
    transform: translateY(-10%);

    @include media(from_m) {
      transform: none;
      height: 100%;
      object-fit: cover;
    }
  }

  &__video-toggler {
    position: absolute;
    top: 100px;
    right: 0;
    width: 72px;
    height: 72px;
    border-radius: 50%;

    background-repeat: no-repeat;
    background-position: center;
    background-size: 14px auto;
    background-image: url("data:image/svg+xml,%3Csvg width='14' height='18' viewBox='0 0 14 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.53326 0.16576L13.5603 8.18016C13.6955 8.27014 13.8064 8.39214 13.8831 8.53532C13.9599 8.67849 14 8.8384 14 9.00082C14 9.16324 13.9599 9.32315 13.8831 9.46632C13.8064 9.60949 13.6955 9.7315 13.5603 9.82148L1.5308 17.8359C1.38247 17.9342 1.2103 17.9905 1.03254 17.9989C0.854785 18.0073 0.67807 17.9674 0.521134 17.8836C0.364199 17.7997 0.232895 17.6749 0.141147 17.5225C0.0493981 17.37 0.000624413 17.1956 0 17.0177V0.983956C0.000329797 0.805655 0.0490254 0.630782 0.140899 0.477959C0.232773 0.325137 0.364383 0.200088 0.521718 0.116128C0.679054 0.0321694 0.856221 -0.00755465 1.03435 0.00118521C1.21248 0.00992508 1.38491 0.0668019 1.53326 0.16576Z' fill='white'/%3E%3C/svg%3E%0A");

    background-color: #004E96;

    @include defaultHover();

    @include media(from_sm) {
      top: 164px;
      width: 118px;
      height: 118px;

      background-size: 22px auto;
    }

    @include media(from_m) {
      top: unset;
      right: unset;
      left: 264px;
      bottom: 32px;
      width: 72px;
      height: 72px;

      background-size: 12px auto;
    }

    @include media(from_l) {
      left: 353px;
      bottom: 42px;
      width: 95px;
      height: 95px;

      background-size: 14px auto;
    }
  }

  &__video-toggler-additional {
    position: absolute;
    top: 0;
    left: 0; 
    width: 100%;
    height: 100%;

    background-repeat: no-repeat;
    background-position: left 55% top 8px;
    background-size: 75% auto;

    @include media(from_sm) {
      background-position: left 60% top 8px;
    }

    @include media(from_m) {
      background-position: left 60% top 6px;
    }

    @include media(from_l) {
      background-position: left 65% top 8px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    box-sizing: border-box;
    padding: 12px 16px 20px;

    @include media(from_sm) {
      padding: 20px 32px 32px 24px;
    }

    @include media(from_m) {
      padding: 20px 104px 40px 56px;
    }

    @include media(from_l) {
      padding: 28px 145px 56px 80px;
    }
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    color: $black;

    @include media(from_sm) {
      font-size: 40px;
    }

    @include media(from_m) {
      margin: 0 0 2px;
      font-size: 22px;
    }

    @include media(from_l) {
      margin: 0 0 4px;
      font-size: 30px;
    }
  }

  &__role {
    margin: 0 0 6px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;

    @include media(from_sm) {
      margin: 0 0 12px;
      font-size: 22px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      margin: 0 0 14px;
      font-size: 14px;
    }
  }

  &__desc {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color_text_secondary;

    @include media(from_sm) {
      font-size: 22px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      font-size: 14px;
    }
  }

  &__regalia-list {
    margin-top: 10px;
    margin-bottom: 20px;

    @include media(from_sm) {
      margin-top: 20px;
      margin-bottom: 32px;
    }

    @include media(from_m) {
      margin-bottom: 16px;
    }

    @include media(from_l) {
      margin-top: 24px;
      margin-bottom: 25px;
    }
  }

  &__regalia-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 12px 16px 12px 8px;
    overflow: hidden;
    border-radius: 10px;
    background: rgba(58, 179, 254, 0.1);

    @include media(from_sm) {
      padding: 18px 20px;
      border-radius: 16px;
    }

    @include media(from_m) {
      padding: 12px 48px 16px 12px; 
      border-radius: 8px;
    }

    @include media(from_l) {
      padding: 16px;
      border-radius: 10px;
    }
  }

  &__regalia-item + &__regalia-item {
    margin-top: 10px;

    @include media(from_sm) {
      margin-top: 20px;
    }

    @include media(from_l) {
      margin-top: 24px;
    }
  }

  &__regalia-icon {
    flex-shrink: 0;
    width: 68px;
    height: 68px;
    margin-right: 12px;

    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    @include media(from_sm) {
      width: 110px;
      height: 110px;
      margin-right: 20px;
    }

    @include media(from_m) {
      width: 48px;
      height: 48px;
      margin-right: 16px;
    }

    @include media(from_l) {
      width: 64px;
      height: 64px;
      margin-right: 24px;
    }
  }

  &__regalia-desc {
    margin: 0;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 13px;
    line-height: 150%;
    color: $color_text_secondary;

    @include media(from_sm) {
      font-size: 22px;
    }

    @include media(from_m) {
      font-size: 10px;
    }

    @include media(from_l) {
      font-size: 14px;
    }
  }

  &__link {
    display: flex;
    align-items: center;

    text-decoration: none;
    color: $blue;
    font-weight: 700;
    font-size: 11px;
    line-height: 120%;

    @include defaultHover();

    @include media(from_sm) {
      font-size: 18px;
    }

    @include media(from_m) {
      margin-left: auto;
      font-size: 12px;
    }

    @include media(from_l) {
      font-size: 16px;
    }

    &::after {
      content: '';
      width: 7px;
      height: 12px;
      margin-left: 8px;

      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;

      background-image: url("data:image/svg+xml,%3Csvg width='8' height='15' viewBox='0 0 8 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.09128 7.5L0 2.05531L1.45436 0.5L8 7.5L1.45436 14.5L0 12.9447L5.09128 7.5Z' fill='%233AB3FE'/%3E%3C/svg%3E%0A");

      @include defaultHover();

      @include media(from_sm) {
        width: 11px;
        height: 20px;
        margin-left: 15px;
      }

      @include media(from_m) {
        width: 7px;
        height: 12px;
        margin-left: 8px;
      }
  
      @include media(from_l) {
        width: 10px;
        height: 16px;
        margin-left: 12px;
      }
    }
  }
}
