@import "@styles/vars.scss";
@import "@styles/animations.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.header {
  position: sticky;
  top: 0;
  z-index: 50;
  background-color: #fff;

  &__container {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    max-width: 1770px;

    box-sizing: border-box;
    padding: 8px 16px;

    @include media(from_sm) {
      padding: 16px 16px 8px;
    }

    @include media(from_m) {
      justify-content: flex-start;
      padding: 8px 16px;
    }

    @include media(from_l) {
      padding: 16px;
    }
  }

  &__logo {
    margin-right: 40px;

    @include media(from_l) {
      margin-right: 80px;
    }
  }

  & .logo__label {
    display: none;

    @include media(from_sm) {
      display: block;
    }
  }

  &__catalog-link {
    min-width: 120px;

    box-sizing: border-box;
    padding: 12px;
    border: 1px solid #3BB4FF;
    background-color: transparent;
    border-radius: 24px;

    text-align: center;
    text-decoration: none;
    color: #3BB4FF;
    font-weight: 700;
    font-size: 14px;;
    line-height: 120%;

    @include fillHover(#3BB4FF, #fff);

    @include media(from_m) {
      min-width: 104px;
      padding: 10px;
      font-size: 12px;
      margin: 0 24px 0 0;
    }

    @include media(from_l) {
      min-width: 128px;
      padding: 14px;
      font-size: 14px;;
      margin: 0 32px 0 0;
    }
  }

  &__menu {
    position: absolute;
    top: 100%;
    left: 0;
    transform: scaleY(0);
    transform-origin: top;

    width: 100%;
    box-sizing: border-box;
    padding: 16px 16px 48px;
    background-color: #fff;

    transition: 0.2s ease-in-out;

    @include media(from_sm) {
      padding: 32px 16px 56px;
    }

    @include media(from_m) {
      position: static;
      display: flex;
      flex-grow: 1;
      align-items: center;
      justify-content: space-between;
      padding: 0;
      transform: scaleY(1);
    }
  }

  &__menu_shown {
    transform: scaleY(1);
  }

  &__burger {
    display: flex;
    align-items: center;
    justify-content: center;
    
    width: 32px;
    height: 32px;

    padding: 0;
    margin: 0 0 0 auto;
    background: none;
    border: none;

    @include defaultHover();

    @include media(from_m) {
      display: none;
    }
  }

  &__burger-icon {
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 24px auto;

    pointer-events: none;

    transition: 0.2s ease-in-out;
  }

  &__burger-icon_type_menu {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='16' viewBox='0 0 24 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.7333 6.73828H1.26667C0.567106 6.73828 0 7.27554 0 7.93829V8.06461C0 8.72736 0.567106 9.26462 1.26667 9.26462H22.7333C23.4329 9.26462 24 8.72736 24 8.06461V7.93829C24 7.27554 23.4329 6.73828 22.7333 6.73828Z' fill='%23CECECE'/%3E%3Cpath d='M22.7333 13.4736H1.26667C0.567106 13.4736 0 14.0109 0 14.6736V14.8C0 15.4627 0.567106 16 1.26667 16H22.7333C23.4329 16 24 15.4627 24 14.8V14.6736C24 14.0109 23.4329 13.4736 22.7333 13.4736Z' fill='%23CECECE'/%3E%3Cpath d='M22.7333 0H1.26667C0.567106 0 0 0.537262 0 1.20001V1.32633C0 1.98907 0.567106 2.52634 1.26667 2.52634H22.7333C23.4329 2.52634 24 1.98907 24 1.32633V1.20001C24 0.537262 23.4329 0 22.7333 0Z' fill='%23CECECE'/%3E%3C/svg%3E%0A");
  }

  &__burger-icon_type_close {
    display: none;

    background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.6389 1.02353L0.759936 13.4337C0.340235 13.8382 0.340472 14.4941 0.760467 14.8988L0.840516 14.9759C1.26051 15.3807 1.94122 15.3809 2.36092 14.9765L15.2398 2.56625C15.6595 2.16182 15.6593 1.50589 15.2393 1.10118L15.1593 1.02404C14.7393 0.619334 14.0586 0.619105 13.6389 1.02353Z' fill='black'/%3E%3Cpath d='M13.639 1.31455L0.760058 13.7248C0.340357 14.1292 0.340594 14.7851 0.760588 15.1898L0.840635 15.267C1.26063 15.6717 1.94134 15.6719 2.36104 15.2675L15.24 2.85726C15.6597 2.45283 15.6594 1.7969 15.2394 1.39219L15.1594 1.31506C14.7394 0.91035 14.0587 0.910121 13.639 1.31455Z' fill='black'/%3E%3Cpath d='M15.2401 13.1425L2.36113 0.732279C1.94143 0.327853 1.26072 0.328081 0.840731 0.732789L0.760682 0.809925C0.340689 1.21463 0.340452 1.87057 0.760153 2.27499L13.6391 14.6852C14.0588 15.0896 14.7395 15.0894 15.1595 14.6847L15.2395 14.6076C15.6595 14.2028 15.6598 13.5469 15.2401 13.1425Z' fill='black'/%3E%3C/svg%3E%0A");
  }

  &__burger-icon_hidden {
    display: none;
    animation: fadeOut 0.2s;
  }

  &__burger-icon_shown {
    display: block;
    animation: fadeIn 0.2s;
  }

  &__nav-wrapper {
    display: flex;
    justify-content: flex-end;
    margin: 0 -16px 48px;
    padding: 0 32px 64px 0;
    border-bottom: 2px solid #DFF3FE;

    background-repeat: no-repeat;
    background-size: 164px auto;
    background-image: url(../../assets/media/header/menu-pic.svg);

    @include media(from_sm) {
      padding: 0 48px 128px;
      margin-bottom: 72px;

      background-size: 220px auto;
    }

    @include media(from_m) {
      align-items: center;
      justify-content: flex-start;
      padding: 0;
      margin: 0 32px 0 0;
      background: none;
      border: none;
    }

    @include media(from_l) {
      margin: 0 48px 0 0;
    }

    @include media(from_xl) {
      margin-right: 48px;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    margin-top: -8px;

    @include media(from_sm) {
      margin-top: -16px;
    }

    @include media(from_m) {
      flex-direction: row;
      align-items: center;
      order: 1;
      margin: 0 0 0 -24px;
    }
  }

  &__nav-link {
    min-width: 104px;
    margin-top: 8px;
    padding: 8px;

    text-decoration: none;
    text-align: right;
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;

    color: #3BB4FF;

    @include defaultHover();

    @include media(from_sm) {
      margin-top: 16px;

      font-size: 14px;;
    }

    @include media(from_m) {
      min-width: unset;
      padding: 0;
      margin: 0 0 0 24px;

      font-size: 12px;
    }

    @include media(from_l) {
      font-size: 14px;;
    }
  }

  &__nav-link_hidden-m {
    @include media(from_m) {
      display: none;
    }
  }

  &__contacts {
    margin-bottom: 40px;

    @include media(from_m) {
      order: 4;
      margin: 0;
    }
  }

  & .contacts__link {
    font-weight: 700;
    font-family: Raleway;
    font-size: 20px;

    @include media(from_m) {
      font-size: 12px;  
      color: $black;
    }

    @include media(from_l) {
      font-size: 16px;;
    }
  }

  & .contacts__label {
    margin: 0 24px 0 0;

    @include media(from_m) {
      display: none;
    }
  }

  &__socials {
    display: flex;
    align-items: flex-end;

    @include media(from_m) {
      order: 2;
    }
  }

  & .socials__partner {
    margin-right: 24px;

    @include media(from_m) {
      display: none;
    }
  }

  & .socials__media {
    @include media(from_l) {
      display: flex;
      margin-left: -8px;
    }
  }

  & .socials__media-item {
    @include media(from_l) {
      margin-left: 8px;
    }
  }

  &__login {
    display: none;

    @include media(from_m) {
      order: 3;
      display: flex;
      align-items: center;
      min-height: 16px;
      padding-left: 24px;
      margin-left: auto;
      margin-right: 40px;

      background-repeat: no-repeat;
      background-position: 0 center;
      background-size: auto 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.76187 0.833008C3.55616 0.833008 0.142822 4.24634 0.142822 8.45206C0.142822 12.6578 3.55616 16.0711 7.76187 16.0711C11.9676 16.0711 15.3809 12.6578 15.3809 8.45206C15.3809 4.24634 11.9676 0.833008 7.76187 0.833008ZM4.00568 13.2368C4.3333 12.5511 6.32949 11.8806 7.76187 11.8806C9.19425 11.8806 11.1981 12.5511 11.5181 13.2368C10.4819 14.0597 9.17901 14.5473 7.76187 14.5473C6.34473 14.5473 5.04187 14.0597 4.00568 13.2368ZM12.6076 12.1321C11.5181 10.8063 8.87425 10.3568 7.76187 10.3568C6.64949 10.3568 4.00568 10.8063 2.91616 12.1321C2.13901 11.1111 1.66663 9.83872 1.66663 8.45206C1.66663 5.09206 4.40187 2.35682 7.76187 2.35682C11.1219 2.35682 13.8571 5.09206 13.8571 8.45206C13.8571 9.83872 13.3847 11.1111 12.6076 12.1321ZM7.76187 3.88063C6.28377 3.88063 5.0952 5.0692 5.0952 6.54729C5.0952 8.02539 6.28377 9.21396 7.76187 9.21396C9.23997 9.21396 10.4285 8.02539 10.4285 6.54729C10.4285 5.0692 9.23997 3.88063 7.76187 3.88063ZM7.76187 7.69015C7.12949 7.69015 6.61901 7.17967 6.61901 6.54729C6.61901 5.91491 7.12949 5.40444 7.76187 5.40444C8.39425 5.40444 8.90473 5.91491 8.90473 6.54729C8.90473 7.17967 8.39425 7.69015 7.76187 7.69015Z' fill='%233BB4FF'/%3E%3C/svg%3E%0A");

      text-decoration: none;
      font-weight: 700;
      font-size: 12px;
      line-height: 120%;
      color: #3BB4FF;

      @include defaultHover();
    }

    @include media(from_l) {
      margin-right: 56px;
      font-size: 14px;
    }
  }
}

.header_expanded {
  & .logo__label {
    display: block;
    animation: fadeIn 0.2s;
  }

  & .header__catalog-link {
    display: none;
    animation: fadeOut 0.2s;

    @include media(from_sm) {
      display: block;
      animation: none;
    }
  }
}
