@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.card-event {
    display: block;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 20px;
    padding: 18px 22px;

    @include media(from_sm) {
        padding: 28px 35px 36px;
    }
    
    @include media(from_m) {
        padding: 24px 12px 24px 28px;
    }
    
    @include media(from_l) {
        padding: 18px 16px 18px 24px;
    }
    
    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center; 
        margin-bottom: -30px;

        &>*{
            margin-bottom: 30px;
        }
    }

    &__date {
        text-align: center;
        display: flex;
        flex-direction: column;
        font-family: 'Open Sans';
        font-weight: 300;
        font-size: 45px;
        line-height: 120%;
        color: #CCCCCC;
        margin-right: 16px;

        
        @include media(from_sm) {
            font-size: 72px;
            margin-right: 30px;
        }
        
        @include media(from_m) {
            font-size: 34px;
            margin-right: 26px;
        }
        
        @include media(from_l) {
            font-size: 45px;
            margin-right: 32px;
        }
        
        span{
            font-weight: 600;
            font-size: 24px;
            line-height: 120%;
            margin-top: -12px;

            
            @include media(from_sm) {
                font-size: 38px;
                margin-top: -18px;
            }
            
            @include media(from_m) {
                font-size: 18px;
                margin-top: -10px;  
            }
            
            @include media(from_l) {
                font-size: 24px;
            }
            
        }
    }

    &__info {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        flex: 1 1 70%;

        @include media(from_l) {
            flex: 0 1 40%;
        }
    }

    &__label {
        margin-bottom: 6px;
        display: none;
        font-family: 'Open Sans';
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        color: #000;
        
        @include media(from_m) {
            display: inline-flex;
            align-items: center;
        }

        &--online{
            padding: 2px 12px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            border-radius: 20px;
            background-color: #00EBBA;
            color: #fff;
        }
    }

    &__tooltip {
        position: relative;
        margin-left: 8px;

        .icon{
            cursor: pointer;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            fill: #B8B8B8;
        }
    }

    &__tooltip:hover &__tooltip-content{
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    &__tooltip-content {
        position: absolute;
        bottom: 50%;
        right: 100%;
        padding: 10px 16px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        z-index: 1;
        transition: opacity 0.4s ease, visibility 0.4s ease;

        @include media(from_m) {
            left: 100%;
        }
    }

    &__title {
        text-decoration: none;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: $blue;

        
        @include media(from_sm) {
            font-size: 25px;
        }
        
        @include media(from_m) {
            font-size: 14px;
            margin-right: 20px;
        }
        
        @include media(from_l) {
            font-size: 18px;
            margin-right: 30px;
        }
    }

    &__price-block {
        margin-right: auto;
        flex: 1 1 50%;
        
        @include media(from_l) {
            margin-top: 0;
            flex-basis: auto;
        }
    }

    &__price {
        position: relative;
        font-family: 'Open Sans';
        font-size: 25px;
        line-height: 120%;
        color: #000;

        
        @include media(from_sm) {
            font-size: 40px;
            color: rgba(0, 0, 0, 0.5);
        }
        
        @include media(from_m) {
            font-size: 22px;
            color: #000;
        }
        
        @include media(from_l) {
            font-size: 30px;
        }

        &--old{
            color: #D9D9D9;
        }
    }

    &__price-label {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateX(25%) rotate(-6deg);
        padding: 6px 14px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        background-color: #F095BA;
        border-radius: 20px;
    }

    &__button {
        .icon{
            display: none;
            
            @include media(from_m) {
                display: flex;
            }
            
            @include media(from_l) {
                
            }
        }
    }
}