@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-events {
    margin-bottom: 136px;
    
    @include media(from_sm) {
        margin-bottom: 188px;
    }
    
    @include media(from_m) {
        margin-bottom: 170px;
    }
    
    @include media(from_l) {
        margin-bottom: 248px;
    }

    &__container {
    }

    &__header {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-bottom: 40px; 

        
        @include media(from_sm) {
            margin-bottom: 65px;
        }
        
        @include media(from_m) {
            margin-bottom: 30px;
            align-items: center;
            flex-direction: row;
        }
        
        @include media(from_l) {
            margin-bottom: 38px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__title {
        text-align: left;
        
        @include media(from_sm) {
            
        }
        
        @include media(from_m) {
            margin-right: 32px;
        }
        
        @include media(from_l) {
            
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__header-badges {
        margin-top: 35px;
        
        @include media(from_sm) {
            margin-top: 40px;
        }
        
        @include media(from_m) {
            margin-top: 0;
        }
        
        @include media(from_l) {
            
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__inner {
    }

    &__main {
    }

    &__list {
        display: flex;
        flex-direction: column;

        @include media(from_m) {
            display: block;
        }
        
        &::after{
            content: "";
            display: block;
            clear: both;
        }
    }

    &__item {
        width: 100%;
        margin-bottom: 16px;
        
        @include media(from_sm) {
            margin-bottom: 26px;
        }
        
        @include media(from_m) {
            float: left;
            width: calc(100% - 200px);
            margin-bottom: 10px;
        }
        
        @include media(from_l) {
            width: calc(100% - 268px);
            margin-bottom: 14px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
        &[data-category="1"]{
            order: 0;
        }
        &[data-category="2"]{
            order: 2;
        }
    }

    &__label{
        display: inline-flex;
        align-items: center;
        margin-bottom: 6px;
        font-family: 'Open Sans';
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        color: #000;
        margin-left: auto;
        margin-bottom: 12px;

        &--online{
            padding: 2px 12px;
            justify-content: center;
            text-align: center;
            border-radius: 20px;
            background-color: #00EBBA;
            color: #fff;
        }

        &[data-category="1"]{
            order: 0;
        }
        &[data-category="2"]{
            order: 2;
        }
        
        @include media(from_sm) {
            
        }
        
        @include media(from_m) {
            display: none;
        }
        
        @include media(from_l) {
        }


    }

    &__tooltip {
        position: relative;
        margin-left: 8px;

        .icon{
            cursor: pointer;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            fill: #B8B8B8;
        }
    }

    &__tooltip:hover &__tooltip-content{
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    &__tooltip-content {
        position: absolute;
        bottom: 50%;
        right: 100%;
        padding: 10px 16px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        z-index: 1;
        transition: opacity 0.4s ease, visibility 0.4s ease;

        @include media(from_m) {
            left: 100%;
        }
    }

    &__aside {
        position: relative;
        display: block;
        width: 100%;
        order: 1;
        margin: 64px 0 80px;
        
        @include media(from_sm) {
            margin: 100px 0 130px;
        }
        
        @include media(from_m) {
            float: right;
            width: 185px;
            flex-shrink: 0;
            margin: 0;
        }
        
        @include media(from_l) {
            width: 248px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__aside-main {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        box-shadow: 0px 0px 44px #E1F4FF;
        padding: 22px 22px 30px;
        border-radius: 20px;
        background-color: $blue;
        color: #fff;
        
        @include media(from_sm) {
            padding: 35px 35px 48px;
        }
        
        @include media(from_m) {
            flex-direction: column;
            flex-wrap: nowrap;
            padding: 28px 15px 20px;
        }
        
        @include media(from_l) {
            padding: 36px 16px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__aside-label{
        top: 0;
        right: -24px;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Open Sans';
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        padding: 8px 24px;
        background-color: #F095BA;
        border-radius: 20px;
        transform: rotate(15deg);

        @include media(from_sm) {
            font-size: 25px;
        }

        @include media(from_m) {
            top: 8px;
            right: 8px;
            font-size: 12px;
            transform: rotate(10deg);
        }

        @include media(from_l) {
            font-size: 16px;
            right: 20px;
            transform: rotate(6deg);
            padding: 8px 16px;
        }
    }

    &__aside-title {
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        letter-spacing: 0.15px;
        margin-bottom: 8px;
        
        @include media(from_sm) {
            margin-bottom: 14px;
            font-size: 29px;
            letter-spacing: 0.25px;
        }
        
        @include media(from_m) {
            font-size: 15px;
            letter-spacing: 0.15px;
            margin-bottom: 20px;

        }
        
        @include media(from_l) {
            font-size: 20px;
        }
        
        @include media(from_xl) {
            
        }
        
        @include media(from_xxl) {
            
        }
        
    }

    &__aside-input {
        width: calc(100% - 80px);

        
        @include media(from_sm) {
            width: calc(100% - 130px);
        }
        
        @include media(from_m) {
            width: 100%;
        }
        
        @include media(from_l) {
            
        }
        
        .icon{
            width: 30px;
            height: 30px;
        }
    }

    &__aside-button {
        padding: 0;
        width: 45px;
        height: 45px;
        margin-left: auto;

        @include media(from_sm) {
            width: 72px;
            height: 72px;
        }

        @include media(from_m) {
            width: auto;
            padding: 0 32px;
            height: 54px;
            margin-left: 0;
            font-size: 12px;
            margin-top: 22px;
        }

        @include media(from_l) {
            margin-top: 35px;
        }
        
        .button{

            &__text{
                display: none;

                @include media(from_m) {
                    display: inline-flex;
                }
            }

            &__icon{
                margin-left: 0;

                @include media(from_m) {
                    margin-left: 15px;
                    width: 24px;
                    height: 24px;
                    margin-left: 6px;
                }
            }
        }
    }

    &__aside-social-block {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-top: 38px;
        width: 100%;
        justify-content: space-between;
        
        span{
            font-family: "Open Sans";
            font-size: 14px;
            line-height: 120%;
            color: #AEAEAE;
            margin-right: 25px;
            margin-bottom: 15px;
            
            @include media(from_sm) {
                font-size: 22px;
            }
            
            @include media(from_m) {
                font-size: 10px;
            }
            
            @include media(from_l) {
                font-size: 14px;
            }
        }
        
        @include media(from_sm) {
            margin-top: 54px;
        }
        
        @include media(from_m) {
            margin-top: 15px;
        }
        
        @include media(from_l) {
            justify-content: center;
            margin-top: 20px;
        }       
    }

    &__aside-social { 
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }

    &__aside-social-item {
        fill: #AEAEAE;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue;
        }

        .icon{
            width: 30px;
            height: 30px;

            @include media(from_sm) {
                width: 47px;
                height: 47px;
            }

            @include media(from_m) {
                width: 32px;
                height: 32px;    
            }

            @include media(from_l) {
                width: 38px;
                height: 38px;
            }
        }
    }
}