@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-questions {
  &__outer-indent {
    margin-bottom: 133px;

    @include media(from_sm) {
      margin-bottom: 200px;
    }

    @include media(from_m) {
      margin-bottom: 130px;
    }

    @include media(from_l) {
      margin-bottom: 196px;
    }
  }

  &__inner {
    width: 100%;

    @include media(from_m) {
      max-width: 680px;
    }
  
    @include media(from_l) {
      max-width: 952px;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 50px;

    @include media(from_sm) {
      margin-bottom: 70px;
    }

    @include media(from_m) {
      flex-direction: row;
      align-items: center;
      margin-bottom: 32px;
    }

    @include media(from_l) {
      margin-bottom: 40px;
    }
  }

  & .section__title {
    margin-bottom: 20px;

    @include media(from_m) {
      margin-bottom: 0;
      margin-right: 64px;
    }

    @include media(from_l) {
      margin-right: 96px;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    margin: -24px 0 0;

    @include media(from_sm) {
      margin: -40px 0 0;
    }
  }

  &__item {
    width: 100%;
    margin: 24px 0 0;

    @include media(from_sm) {
      margin: 40px 0 0;
    }

    @include media(from_m) {
      margin: 40px 0 0;
    }
  }

  &__resolution {
    margin-top: 96px;

    @include media(from_sm) {
      margin-top: 56px;
    }

    @include media(from_m) {
      margin-top: 64px;
    }

    @include media(from_l) {
      margin-top: 88px;
    }
  }
}
