@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.card-review {
    position: relative;
    display: block;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    min-height: 392px;
    color: #000;
    padding: 55px 16px 12px 36px;
    background-color: #fff;
    border-radius: 20px;
    overflow: hidden;
    

    &::before{
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        position: absolute;
        top: 20px;
        left: 10px;
        background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.929688 10.4258L9.23047 0.44043L12.1357 2.10059L5.12891 10.7188L12.1357 19.3369L9.23047 21.0215L0.929688 11.085V10.4258ZM10.6465 10.4258L19.0693 0.44043L21.9258 2.10059L14.9434 10.7188L21.9258 19.3369L19.0693 21.0215L10.6465 11.085V10.4258Z' fill='%23CCCCCC'/%3E%3C/svg%3E%0A");
        z-index: 2;
    }

    &::after{
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: 50px;
        height: 50px;
        background-image: url("data:image/svg+xml,%3Csvg width='50' height='50' viewBox='0 0 50 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3 0H50V49H23C11.9543 49 3 40.0457 3 29V0Z' fill='%23D5F0FF'/%3E%3Cpath d='M3 0H50V49L3 0Z' fill='%23F1FAFF'/%3E%3C/svg%3E%0A");
    }
    
    &__inner {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__body {
        box-sizing: border-box;
        position: relative;
        max-height: 232px;
        overflow: hidden;
        padding-right: 10px;
        transition: max-height 0.5s ease;
    }

    &__content {
        box-sizing: border-box;
    
        
        &>*:last-child{
            margin-bottom: 0;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 16px;
        line-height: 130%;
        margin-bottom: 12px;
    }

    &__text {
        font-family: 'Inter';
        font-size: 12px;
        line-height: 150%;
        color: #666666;
        margin-bottom: 15px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__more {
        cursor: pointer;
        position: absolute;
        bottom: 0;
        right: 0;
        display: none;
        align-items: flex-end;
        justify-content: flex-end;
        width: 100%;
        height: 30px;

        .icon{
            width: 30px;
            height: 15px;
        }

        &.is-visible{
            display: flex;
        }
    }

    &__link {
        position: relative;
        font-weight: 700;
        font-family: "inter";
        font-size: 12px;
        line-height: 150%;
        margin: 12px 0 12px;

        &::after{
            pointer-events: none;
            content: "";
            display: block;
            width: 20px;
            height: 20px;
            position: absolute;
            top: 50%;
            transform: translate(50%,-50%);
            right: 0;
            z-index: 2;
            background-image: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.0703 11.0146L12.7695 21L9.86426 19.3398L16.8711 10.7217L9.86426 2.10351L12.7695 0.418945L21.0703 10.3555L21.0703 11.0146ZM11.3535 11.0146L2.93066 21L0.0742189 19.3398L7.05664 10.7217L0.0742204 2.10351L2.93067 0.418944L11.3535 10.3555L11.3535 11.0146Z' fill='%23CCCCCC'/%3E%3C/svg%3E%0A");
        }
    }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        position: relative;
        z-index: 2;
        margin-top: auto;

    }

    &__name {
        display: inline-flex;
        text-align: right;
        font-family: 'Inter';
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.15px;
        color: #4F4F4F;
    }

    &__author {
        margin-left: 12px;
        width: 48px;
        height: 48px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        background-color: $blue;
    }

    &__author-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__video-wrapper {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;

        &::after{
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            background:linear-gradient(180deg, rgba(57, 184, 255, 0) 0%, #39B8FF 100%);
        }
    }

    &--video{
        &::before, 
        &::after{
            content: none;
        }
    }

    &--video &__name{
        color: #fff;
    }
}

